.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
}

.container2 {
  background-position: center;
  display: flex;
  height: 200px; 
}

.overlay {
  background-color: tan;
  box-sizing: border-box;
  font-size: 50px;
  height: 700px;
  bottom: -700px;
  left: 0;
  opacity: 0;
  position: absolute;
  transition: bottom 10s ease;
  width: 100%;
}

  img {
    width: 100%;
    height: 100%;
  }

.item {
  position: relative;
  height: 900px;
  overflow: hidden;
  flex: 1;
  flex-basis: 700px;
}

//.item:hover .overlay {
  bottom: 0;
  opacity: 1;
}

.item2 {
  padding: 30px;
  font-size: 60px;
  font-weight: bolder;
  flex: 1;
  height: 200px;
  color: black;
}

.item3 {
  font-size: 60px;
  font-weight: bolder;
  padding: 30px;
  flex: 1;
  height: 100px;
}

.item4 {
  height: 150px;
  padding: 20px;
  text-align: center;
}
a {
  text-decoration:none;
  color: black;
}